/***********************************************
 * General tweaks for nicer appearance
 */
@import "settings";
body {
  color: $mainColor;
  margin-top: 0; // Let header image attach to the top of the screen.
  font-family: $mainFont;
  * {
    font-variant: proportional-nums oldstyle-nums;
  }
}
// Align header image with content
h1 > img {
  margin-top: -9rem;
  width: 100%;
}
// Correct width of some elements
details,
div.notes {
  @extend section;
}
li > dl {
  font-style: italic;
  &, & > p {
    width: 100%;
  }
}

/***********************************************
 * Headings and lists
 */
// Don't let headings get caught in floats
h1, h2, h3, p.subtitle {
  clear: both;
  font-family: $headingFont;
  font-variant: lining-nums titling-caps;
  font-weight: $headingFontWeight;
  &.unlisted {
    clear: none;
  }
}
h1 {
  font-size: $heading1Size;
  font-style: normal;
  letter-spacing: $headingLetterSpacing;
  text-transform: $headingTextTransform;
}
h2,
p.subtitle {
  font-family: $mainFont;
  font-size: $heading2Size;
  font-style: italic;
}
// Uniform styling of lesser headings
dt, dt > a, h3, h4, h5 {
  @extend .smallcaps;
  clear: left;
  font-size: $heading3Size;
  font-style: normal;
  font-weight: 400;
  letter-spacing: $headingLetterSpacing * 0.25;
}
// Run-in definition lists and headings
article > dl,
section > dl {
  clear: left;
  font-size: $heading3Size;
  line-height: 2rem;
  width: 55%;
}
dt, h5, h6 {
  float: left;
  margin: 0 1.4rem 0 0;
  padding: 0;
  line-height: 1.5rem;
}
h5, h6,
li > dl > dt,
dl > dt:first-of-type {
  line-height: 2rem; // Fix some edge conditions
}
h3, h5,
span.newthought {
  font-size: $heading2Size;
}
h5,
span.newthought {
  margin-top: -0.1rem;
}
// Outset list markers (eyeballed), tabular figures, Roman SC
article ol,
article ul {
  margin-left: -2.5rem;
  width: 55%;
  -webkit-padding-start: 2.5rem;
  & > li::marker {
    font-variant: tabular-nums oldstyle-nums all-small-caps;
  }
}
ul {
  list-style-type: none;
  li {
    text-indent: -1.5rem;
    &::before {
      content: "– ";
    }
  }
}
// Pandoc's line-break blocks
.line-block {
  font-size: $heading3Size;
  line-height: 2rem;
}
// Outset dates in résumé
.cv dt {
  font-variant: tabular-nums oldstyle-nums all-small-caps;
  margin-left: -8.5rem;
}

/***********************************************
 * General formatting improvements
 */
// Reverse emphasis
h1,
h2,
p.subtitle,
div.epigraph > blockquote > p {
  em {
    font-style: normal;
  }
}
// Substitute vspace for horizontal rule
hr {
  border: 0;
  clear: left;
  text-align: center;
  font-size: $heading1Size;
  padding: $heading4Size/2 0;
  //&::before {
    //content: "* * *";
  //}
}
// Thin border around images
iframe,
img,
video {
  border: 0.5px solid $mainColor;
  &.nobg,
  &.line {
    background: none !important;
    border: none !important;
  }
}
figure > video {
  max-width: 100%;
}
// Proper alignment of details > summary
summary > p {
  display: inline;
}
// Sensible table borders
table {
  border-collapse: collapse;
  border-top: 2px solid $mainColor;
  border-bottom: 2px solid $mainColor;
  margin: 2rem 0;
  width: 55%;
  caption {
    font-size: $heading4Size;
    font-weight: 500;
    margin-bottom: 1rem;
  }
  th, td {
    font-size: $heading3Size;
    font-variant: oldstyle-nums tabular-nums;
  }
  th {
    font-weight: 600;
    border-bottom: 1px solid $mainColor;
  }
  td {
    border-top: 1px solid $mainColor;
    vertical-align: top;
    & > p {
      margin-top: 0; // Flush table cell contents top even if it has a p
    }
  }
}
code {
  border: 0.5px solid $mainColor;
  border-radius: 0.5rem;
  padding: 0.25rem;
}
blockquote > footer,
sup {
  font-weight: 500;
}

/***********************************************
 * Pandoc+Citeproc improvements
 */
// Improve position + style of Pandoc citations
section > blockquote > p > span.citation > span.sidenote-wrapper > span.sidenote {
  margin-right: -70%;
}
// Marginnotes
.author {
  width: 55%; // Keeps author affiliation within viewport
}
.marginnote,
.sidenote {
  hyphens: auto;
  margin-bottom: 1rem;
  background: transparentize($backgroundColor, 0.3);
}
.marginnote,
.sidenote,
.sidenote-number,
.sidenote-number::after,
.sidenote::before {
  font-family: $mainFont !important; // Prevent Pandoc-Tufte from falling back on ET-Book.
  font-style: normal;
  font-weight: 500;
}
// When I say small caps, I mean it.
.smallcaps,
span.newthought {
  font-variant: all-small-caps;
}

/******************************************************
 * CSL styling taken from Tufte-CSS, not yet
   implemented in Jekyll-Pandoc-Tufte.
 */
div.csl-bib-body {
    width: 55%;
    font-size: $heading3Size;
    line-height: 2rem;
}
div.csl-entry {
    clear: both;
    margin-top: .5rem;
}
.hanging div.csl-entry {
    margin-left: 2rem;
    text-indent: -2rem;
}
div.csl-left-margin {
    min-width: 2rem;
    float: left;
}
div.csl-right-inline {
    margin-left: 2rem;
    padding-left: 1rem;
}
div.csl-indent {
    margin-left: 2rem;
}
div.hanging-indent{
    margin-left: 1.5rem;
    text-indent: -1.5rem;
}

/******************************************************
 * Fixes for pandoc-crossref
 */
// Correct handling of figures with captions
figcaption {
  @extend .marginnote;
  max-width: 50%;
}
p~figure > img.marginnote,
div.notes~figure > img.marginnote {
  margin-top: 1.6rem; // Fixes an edge case of figure/figure.marginnote alignment.
}
div.r-stack > figure {
  // Right now .r-stack figures are hard-coded to show up in two equal
  // columns.
  float: left;
  max-width: 50%;
  & > figcaption {
    float: none;
    max-width: 100%;
    width: 100%;
  }
}
figure > img.fullwidth,
figure > video.fullwidth {
  clear: both;
  margin-top: 2rem;
  max-width: 160%; // Empirically determined to line up with other elements.
}
.subfigures,
.iframe-wrapper {
  & > iframe {
    max-width: 55%;
  }
  figcaption {
    text-align: right;
  }
  & > p:last-of-type,
  &.fullwidth > p:last-of-type {
    @extend figcaption;
    text-align: left;
    margin-bottom: 2em;
    margin-right: 12.5%; // Empirically determined to line up with other elements.
    max-width: 27.5%; // column width of 55% * 50% width for captions
  }
  &.fullwidth > p:last-of-type {
    margin-top: 1em;
    margin-right: 2.5%;
    max-width: 30%;
  }
}
.fullwidth > iframe {
  max-width: 100%;
}

/******************************************************
 * Fixes for reveal.js elements
 */
h3.unlisted {
  margin: 0;
}
.columns {
  clear:both;
  width: 92.5%; // Empirically determined to line up with other elements.
  &.subfigures {
    img {
      float: left;
    }
    p {
      margin-right: 5%;
      max-width: 30%; // 50% + 5% margin on either side = 60% * 50%
    }
  }
}
.column {
  float: left;
  figure {
    margin-bottom: 1em;
    max-width: 90%;
    figcaption {
      margin-left: 0;
      margin-right: 0;
      max-width: 66%;
      width: 66%;
    }
  }
}
.incremental {
  @extend section;
}

/******************************************************
 * Extending Tufte-Pandoc-Jekyll
 */
#main-nav,
#lang-select,
p.signoff {
  font-size: $heading4Size;
  margin-top: 1rem;
  a.icons:link {
    background: none;
    text-shadow: none;
    &::after {
      content: "  ";
    }
  }
}
#lang-select {
  float: right;
  font-weight: 500;
  margin-right: 45%;
  margin-top: 0.5rem;
}
.blogroll {
  width: 55%;
}
nav.pagination > ul,
ul.blogroll h3 {
  display: inline;
}
ul.contact > li::before {
  content: "";
}
nav.pagination {
  list-style-type: none;
  & > ul > li {
    display: inline;
    text-indent: 0;
    &::before {
      content: "";
      font-variant: tabular-nums;
    }
    &::after {
      content: " | ";
    }
    &:last-child::after {
      content: "";
    }
  }
}

/******************************************************
 * Light and Dark
 */
@media (prefers-color-scheme: no-preference), // Default to light
       (prefers-color-scheme: light) {
	body {
    color: $mainColor;
    background-color: $backgroundColor;
    .en:lang(en), .fr:lang(fr), .pt:lang(pt){
      color: transparentize($mainColor, 0.5);
    }
    a:link {
      text-decoration: none;
      background: -webkit-linear-gradient($backgroundColor, $backgroundColor), -webkit-linear-gradient($backgroundColor, $backgroundColor), -webkit-linear-gradient(lighten($mainColor, 15%), lighten($mainColor, 15%));
      background: linear-gradient($backgroundColor, $backgroundColor), linear-gradient($backgroundColor, $backgroundColor), linear-gradient(lighten($mainColor, 15%), lighten($mainColor, 15%));
      -webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
      -moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
      background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
      background-repeat: no-repeat, no-repeat, repeat-x;
      text-shadow: 0.03em 0 $backgroundColor, -0.03em 0 $backgroundColor, 0 0.03em $backgroundColor, 0 -0.03em $backgroundColor, 0.06em 0 $backgroundColor, -0.06em 0 $backgroundColor, 0.09em 0 $backgroundColor, -0.09em 0 $backgroundColor, 0.12em 0 $backgroundColor, -0.12em 0 $backgroundColor, 0.15em 0 $backgroundColor, -0.15em 0 $backgroundColor;
      background-position: 0% 93%, 100% 93%, 0% 93%;
    }
	}
}
@media (prefers-color-scheme: dark) {
  body {
    color: $backgroundColor;
    background-color: lighten($mainColor, 10%);
    .en:lang(en), .fr:lang(fr), .pt:lang(pt){
      color: transparentize($backgroundColor, 0.5);
    }
    a:link {
      text-decoration: none;
      background: -webkit-linear-gradient($mainColor, $mainColor), -webkit-linear-gradient($mainColor, $mainColor), -webkit-linear-gradient(lighten($backgroundColor, 10%), lighten($backgroundColor, 10%));
      background: linear-gradient($mainColor, $mainColor), linear-gradient($mainColor, $mainColor), linear-gradient(lighten($backgroundColor, 10%), lighten($backgroundColor, 10%));
      -webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
      -moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
      background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
      background-repeat: no-repeat, no-repeat, repeat-x;
      text-shadow: 0.03em 0 $mainColor, -0.03em 0 $mainColor, 0 0.03em $mainColor, 0 -0.03em $mainColor, 0.06em 0 $mainColor, -0.06em 0 $mainColor, 0.09em 0 $mainColor, -0.09em 0 $mainColor, 0.12em 0 $mainColor, -0.12em 0 $mainColor, 0.15em 0 $mainColor, -0.15em 0 $mainColor;
      background-position: 0% 93%, 100% 93%, 0% 93%;
    }
    img {
      // Prevents line drawings as transparent PNGs from disappearing
      background-color: lighten($mainColor, 10%);
    }
    table {
      border-top: 2px solid $backgroundColor;
      border-bottom: 2px solid $backgroundColor;
      td {
        border-top: 1px solid $backgroundColor;
      }
      th {
        border-bottom: 1px solid $backgroundColor;
      }
    }
    .line {
      filter: invert(83%);
      -webkit-filter: invert(83%);
    }
    .marginnote,
    .sidenote {
      background: transparentize(lighten($mainColor, 10%), 0.2);
    }
  }
}

/******************************************************
 * Screen width overrides must come last.
 */
.marginnote-directions {
  display: none;
}
@media (max-width: 760px) {
  article,
  article p,
  article dl,
  article ol,
  article ul,
  details,
  dl,
  div.blogroll,
  div.csl-bib-body,
  div.column,
  div.column > figure,
  div.column figure figcaption,
  div.columns.subfigures > p,
  div.incremental,
  div.marginnote-directions,
  figure > img.fullwidth,
  figure > video.fullwidth,
  figcaption,
  header > div.epigraph,
  img.marginnote,
  p.author {
    max-width: 100%;
    width: 100% !important;
  }
  figure {
    max-width: 100%;
  }
  img.marginnote,
  figcaption,
  .column figure figcaption,
  .columns.subfigures > p,
  .marginnote-directions {
    display: inline;
    clear: both;
    float: none;
  }
}

@media (max-width: 960px) {
  .cv dt {
    margin-left: -1.5rem;
    &::before {
      content: "– ";
    }
  }
}
